.backdrop {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  z-index: 999;
  background: #2e2f4266;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal {
  background-color: var(--background-color);
  overflow: auto;
  transform: scale(0);
  box-shadow: 0 1px 1px #00000024, 0 1px 3px #0000001f, 0 2px 1px #0003;
}

@media screen and (width >= 768px) {
  .modal {
    top: 50%;
    overflow: visible;
  }
}

.btn-close-modal {
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.hidden {
  clip: rect(0, 0, 0, 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

#preloader {
  z-index: 1001;
  opacity: 1;
  visibility: visible;
  background-color: #0f0e0dea;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  margin: 0;
  transition: all .25s linear;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#preloader.done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.pre-loader-container {
  z-index: 1001;
  opacity: 1;
  visibility: visible;
  justify-content: center;
  align-items: center;
  width: 20em;
  height: 20em;
  font-size: 10px;
  display: flex;
  position: relative;
}

.pre-loader-container.done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.pre-loader-container .face {
  border-style: solid;
  border-radius: 50%;
  animation: 3s linear infinite animate;
  position: absolute;
}

.pre-loader-container .face:first-child {
  color: #f80;
  --deg: -45deg;
  border-width: .2em .2em 0 0;
  border-color: currentColor #0000 #0000 currentColor;
  width: 100%;
  height: 100%;
  animation-direction: normal;
}

.pre-loader-container .face:nth-child(2) {
  color: gold;
  --deg: -135deg;
  border-width: .2em 0 0 .2em;
  border-color: currentColor currentColor #0000 #0000;
  width: 70%;
  height: 70%;
  animation-direction: reverse;
}

.pre-loader-container .face .circle {
  transform: rotate(var(--deg));
  transform-origin: 0;
  background-color: #0000;
  width: 50%;
  height: .1em;
  position: absolute;
  top: 50%;
  left: 50%;
}

.pre-loader-container .face .circle:before {
  content: "";
  background-color: currentColor;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  position: absolute;
  top: -.5em;
  right: -.5em;
  box-shadow: 0 0 2em, 0 0 4em, 0 0 6em, 0 0 8em, 0 0 10em, 0 0 0 .5em #ffff001a;
}

@keyframes animate {
  to {
    transform: rotate(1turn);
  }
}

.scroll-top-container {
  z-index: 999;
  position: fixed;
  bottom: 40px;
