@property --light-dark-toggle-x {
  syntax: "<percentage>";
  inherits: false;
  initial-value: 0%;
}

.light-dark {
  appearance: none;
  aspect-ratio: 2;
  background-color: #87b0ff;
  background-image: linear-gradient(#0001, #0000, #0000, #0001);
  border-radius: 100em;
  width: 4em;
  font-size: 1em;
  transition: background-color .4s;
  position: relative;
}

.light-dark:before {
  --light-dark-toggle-x: 0;
  content: "";
  aspect-ratio: 1;
  -webkit-mask: radial-gradient(circle at 20% var(--light-dark-toggle-x), #0000 var(--light-dark-toggle-x), #000 0);
  background: #ffc;
  border-radius: 50%;
  height: 60%;
  transition: all .5s, --light-dark-toggle-x .4s;
  position: absolute;
  top: 50%;
  left: 25%;
  transform: translate(-50%, -50%);
}

.light-dark:after {
  content: "";
  aspect-ratio: 1;
  clip-path: polygon(-200% 0, 300% 0, 300% 80%, -200% 80%);
  background: #fff;
  border-radius: 50%;
  width: 1em;
  transition: width .4s linear, top .4s linear, left .4s linear, clip-path .2s linear, box-shadow .3s cubic-bezier(.75, 1.35, .75, .75);
  position: absolute;
  top: 40%;
  left: 70%;
  transform: translate(-50%, -50%);
  box-shadow: -.5em .25em 0 -.1em #fff, .5em .3em 0 -.2em #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff;
}

.light-dark:checked {
  background-color: #050916;
}

.light-dark:checked:before {
  --light-dark-toggle-x: 35%;
  left: 75%;
}

.light-dark:checked:after {
  clip-path: polygon(-3000% -2000%, 2000% -2000%, 2000% 300%, -3000% 300%);
  width: .15em;
  top: 50%;
  left: 33%;
  box-shadow: -.5em .25em 0 -.02em #fff, .5em .3em 0 -.01em #fff, .7em -.285em 0 -.01em #fff, .74em -.5em 0 -.03em #fff, -.3em -.35em 0 -.02em #fff, 0 -.06em 0 -.04em #fff, -.04em .045em 0 -.04em #fff, .05em .045em 0 -.04em #fff;
}

.light-dark[disabled] {
  opacity: .25;
}

@media print {
  .light-dark, .light-dark:before, .light-dark:after {
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact;
  }
}

@media (prefers-reduced-motion) {
  .light-dark, .light-dark:before, .light-dark:after {
    transition: none !important;
    animation: none !important;
  }
}

